/*ResetBootstrap*/
html{
	height: 100%;
}
body{
    font-family: 'Roboto Condensed', sans-serif;
    color:#555;
    background:#f3f3f3;
    height: 100%;
}
@media(min-width:1690px){
    .container{
    max-width:1660px;
    }
}
a:focus,
button:focus,
input:focus{
	outline: 0;
}
/*Header*/
.site-header-row{
	@media(min-width: 768px){
	height: 60px;
}
}
.header-nav-col{
		@media(min-width: 768px){
	padding-top:20px;
}
	@media(min-width: 1200px) and (max-width: 1689px){
		-ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
	margin-left:0;

	}
}
.header-nav{
	ul{
		list-style: none;
		padding-left: 0;
		margin-bottom: 0;
	}
	&>ul{
	display:flex;
		@media(max-width: 767px){
		flex-direction: column;
		padding-top:10px;
	}
	&>li:not(:last-child){
			@media(min-width: 768px) and (max-width: 1199px){
		margin-right:30px;
	}
		@media(min-width: 1200px){
		margin-right: 50px;
	}
	}
	a{
			@media(min-width: 768px) and (max-width: 1199px){
		font-size: 24px;
	}
		@media(min-width: 1200px){
		font-size: 28px;
	}
		font-weight: 700;
		color: #00ADB4;
		text-transform: uppercase;
		transition: color 0.3s ease;
		@media(max-width: 767px){
		display:block;
		padding:10px 0;
		&:hover,
		&:focus{
			text-decoration: none;
		}
		&:active{
			color:#FE6606;
			text-decoration: none;
		}
		}
		@media(min-width: 768px){
		&:hover,
		&:focus{
			color:#FE6606;
			text-decoration: none;
		}
		}
	}
	}	
}
.pictured-bg .header-nav a{
	color:#fff;
}
.header-icons-col{
	display: flex;
	flex-direction: column;
	a{
		margin-right: 50px;
		transition: opacity 0.3s ease;
		&:hover,
		&:focus{
opacity:0.6;
		}
	}
}
.pictured-bg .juice-white-col-wrap{
	background: transparent;
}
.header-icons-col .juice-white-col-wrap{
	@media(max-width: 991px){
		background: transparent;
	}
	display: flex;
	justify-content: flex-end;
	padding-top:20px;
	height: 100%;
}
.mobile-header{
@media(max-width:575px){
	height:50px;
}
@media(min-width:576px) and (max-width:767px){
	height:60px;
}
@media(max-width:767px){
	display: flex;
	justify-content: space-between;
	align-items: center;
}
}
.mobile-header-i{
	@media(max-width: 767px){
	transition: opacity 0.3s ease;
	&:active{
		opacity:0.6;
	}
	}
	img{
		@media(max-width: 575px){
			height:23px;
		}
		@media(min-width: 576px) and (max-width:767px){
			height: 30px;
		}
	}
}
.mobile-menu-toggler{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width:40px;
	height:40px;
	border-radius: 4px;
	background: transparent;
	border:1px solid transparent;
	padding:0;
	span{
		height:2px;
		width:32px;
		background: #4d4d4d;
		&:not(:last-child){
			margin-bottom:6px;
		}
	}
	&:active{
		border-color:#00ADB4;
		span{
			background: #00ADB4;
		}
	}
}
.pictured-bg .mobile-menu-toggler{
	span{
		background: #fff;
	}
	&:active{
		border-color:rgba(255,255,255,0.85);
		span{
			background: rgba(255,255,255,0.85);
		}
	}
}
.mobile-logo{
	@media(max-width: 575px){
		width:65px;
	}
	@media(min-width: 576px) and (max-width: 767px){
		width:80px;
	}
}
/*content*/
img{
	max-width: 100%;
}
.juice-white-col-wrap{
	background:#fff;
}
.site-wrapper{
	display: flex;
	flex-direction: column;
	height: 100%;
}
.site-content{
	flex-grow: 1;
}
.site-content-row{
	height: 100%;
}
.juice-thumb-col{
	display: flex;
	justify-content: center;
	align-items: center;
	@media(max-width: 991px){
		flex-direction: column;
	}
	@media(max-width: 767px){
		padding:15px;
	}
	@media(min-width: 768px){
		padding:30px 60px;
	}
	
	img{
		position: relative;
		max-height: 100%;
	}
}
.juice-thumbs{
position: relative;
&:before{
	content:'';
		@media(min-width: 768px) and (max-width: 991px){
	width:565px;
	height:565px;
	}
		@media(min-width: 992px) and (max-width: 1199px){
	width:415px;
	height:415px;
	}
		@media(min-width: 1200px) and (max-width: 1689px){
	width:515px;
	height:515px;
	}

	@media(min-width: 1690px){
	width:615px;
	height:615px;
	}

	background: #fff;
	border-radius: 100%;
	position: absolute;
	top:50%;
	left:50%;
	transform:translate(-50%,-50%);
}
}
.juice-descr-col{
	@media(max-width: 991px){
		padding-bottom: 15px;
	}
	.juice-white-col-wrap{
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100%;
}	
}
.juice-descr-container{
@media(min-width: 1690px){
padding:30px 138px;
}
@media(min-width: 768px) and (max-width: 1689px){
padding:30px;
}
@media(max-width: 767px){
padding:15px;
}
}
.juice-descr-title{
font-weight: bold;
@media(max-width: 767px){
font-size:25px;
}
@media(min-width: 768px) and (max-width: 1199px){
font-size:40px;
}
@media(min-width: 1200px){
font-size: 50px;
}

text-transform: uppercase;
line-height: 1.1;
margin-bottom: 0;
}
.juice-descr-subtitle{
@media(max-width: 767px){
font-size:18px;
margin-bottom:5px;
}
@media(min-width: 768px) and (max-width: 1199px){
font-size:22px;
margin-bottom:5px;
}
@media(min-width: 1200px){
font-size: 26px;
margin-bottom: 15px;
}
text-transform: uppercase;
color: #00ADB4;

}
.juice-description{
	@media(max-width: 1199px){
		margin-bottom:20px;
	}
	@media(min-width: 1200px){
	font-size: 18px;
		margin-bottom: 40px;
	}


p:last-child{
	margin-bottom:0;
}
}
.juice-descr-item{
	&:not(:last-child){
	@media(max-width: 1199px){
		margin-bottom:20px;
	}
			@media(min-width: 1200px){
		margin-bottom: 40px;
	}
	}
}
.juice-descr-item-title{
font-weight: bold;
@media(min-width: 1200px){
font-size: 20px;
margin-bottom:10px;
}
@media(max-width: 1199px){
font-size:18px;
&+.juice-descr-taste-item{
	margin-top: 5px;
}
}
text-transform: uppercase;
}
.juice-descr-taste-item{
font-size: 18px;
display: flex;
align-items: center;
&:not(:last-child){
	@media(max-width: 991px){
	margin-bottom:15px;
	}
	@media(min-width: 992px){
	margin-bottom: 20px;
	}	
}
}
.juice-taste-color{
width:30px;
height:30px;
border-radius: 100%;
@media(max-width: 1199px){
margin-right:10px;
}
@media(min-width: 1200px){
margin-right: 20px;
}
&.dark-green{
	background: #558B0D;
}
&.light-green{
	background: #CDEC7A;
}
&.red{
	background: #DD1A0A;
}
&.dark-red{
	background: #B13B17;
}
&.violet{
	background:#6B49CA;
}
&.yellow{
	background:#FFDD55;
}
&.light-brown{
	background:#FFA325;
}
}
.juice-descr-item-content{
	@media(min-width: 1200px){
	font-size: 18px;
	}
p:last-child{
	margin-bottom:0;
}
}
.size-descr-item{
	.juice-descr-item-title{
		@media(max-width: 991px){
		margin:0 5px 0 0;
		}
		@media(min-width: 992px){
		margin:0 20px 0 0;
		}
		
	}
display: flex;
align-items: center;
}
.juice-size-wrap{
	position:relative;
	font-size: 20px;
}
.juice-size-current{
position: relative;
		@media(max-width: 991px){
padding-right:20px;
}
		@media(min-width: 992px){
padding-right:30px;
}
cursor: pointer;
transition: color 0.3s ease;
&:before{
	content:'';
	position: absolute;
	right:0;
	top:50%;
	transform: translate(0,-50%);
	width:15px;
	height:8px;
	background:url(../img/i/shevron-down.svg) no-repeat 0 0;
	background-size: contain;
}
&:hover{
	color:#00ADB4;
}
}
.juice-size-options{
display: none;
position: absolute;
z-index: 1;
background:#fff;
border:1px solid #00ADB4;
a{
	font-size: 20px;
	display: block;
	padding:10px 15px;
	color:#555;
	transition: all 0.3s ease;
	&:hover,
	&:focus{
		color:#fff;
		text-decoration: none;
		background:#00ADB4;
	}
}
}
.juice-facts-title{
display: inline-block;
position: relative;
font-weight: bold;
@media(min-width: 1200px){
font-size: 20px;
}
@media(max-width: 1199px){
font-size:18px;
}
text-transform: uppercase;
cursor: pointer;
padding-right:30px;
transition: color 0.3s ease;
&:hover{
	color:#00ADB4;
}
span{
	position: absolute;
	right:0;
	top:50%;
	transform: translate(0,-50%);
	width:14px;
	height:14px;	
}
span:before{
	content:'';
	display: block;
	width:14px;
	height:14px;
	background:url(../img/i/plus.svg) no-repeat 0 0;
	background-size: contain;
	transition: transform 0.3s ease;
}
&[aria-expanded="true"]{
	span:before{
		 transform-origin: 50%;
		transform:rotate(45deg);
	}
}
}
.juice-facts-wrap{
	@media(min-width: 1200px){
	font-size: 18px;
	}

p:last-child{
	margin-bottom:0;
}
padding-top:10px;
}
.pictured-bg{
	background:url(../img/tropic-bg.jpg) no-repeat 50% fixed;
}
.juice-pictured-text-col{
display: flex;
align-items: center;
padding:15px;
@media(min-width: 1200px) and (max-width: 1689px){
margin-left: 0;
-ms-flex: 0 0 60%;
flex: 0 0 60%;
max-width: 60%;
}
}
.white-wrapped-text{
	@media(min-width:992px){
	font-size: 18px;
}
	@media(max-width:575px){
	padding:15px;
}
	@media(min-width:576px){
	padding:40px;
}
background: rgba(255,255,255,0.8);
@media(max-width: 767px){
border-radius:15px;
}
@media(min-width: 768px){
border-radius: 50px;
}

p:last-child{
	margin-bottom:0;
}
h1{
	@media(max-width:767px){
		font-size: 26px;
	}
	@media(min-width:1200px){
		font-size: 50px;
	}

	font-weight: 700;
	text-transform: uppercase;
	margin-bottom: 10px;
}
}